import axios from 'axios';

class Youtube {
    constructor(key) {
        this.youtube = axios.create({
            baseURL: 'https://www.googleapis.com/youtube/v3',
            params: {key: key},
        });
    }

    // async mostPopular () {
    //     const response = await this.youtube.get('videos', {
    //         params : {
    //             part: 'snippet, statistics',
    //             chart: 'mostPopular',
    //             maxResults: 28,
    //             regionCode: 'kr',
    //         },
    //     });
    //     return response.data.items;
    //     // const response = await fetch(`https://www.googleapis.com/youtube/v3/videos?part=snippet,statistics&Members=list&chart=mostPopular&maxResults=28&regionCode=kr&key=${this.key}`, this.getRequestOptions);
    //     // const result_1 = await response.json();
    //     // return result_1.items;
    // }
    async mostPopular() {
        const response = await this.youtube.get("videos", {
          params: {
            part: "snippet",
            chart: "mostPopular",
            maxResults: "28",
            regionCode: "KR",
          },
        });
    
        this.finalList = [];
        response.data.items.map((item) => {
          return this.finalList.push(this.channel(item.snippet.channelId, item));
        });
        return Promise.all(this.finalList).then((values) => values);
        
      }


    // 
    async search(query) {
        const channelList = [];
        const newList = [];
        const response = await this.youtube.get("search", {
          params: {
            part: "snippet",
            q: query,
            maxResults: "25",
            type: "video",
          },
        });
        const changedId = [];
    
        response.data.items.map((item) => {
          return changedId.push({ ...item, id: item.id.videoId });
        });
    
        this.finalList = [];
    
        changedId.map((item) => {
          return this.finalList.push(this.channel(item.snippet.channelId, item));
        });
        return Promise.all(this.finalList).then((values) => values);
      }
    
      async channel(id, videos) {
        const response = await this.youtube.get("channels", {
          params: {
            part: "snippet, statistics",
            id,
          },
        });
    
        if (response.data.items[0].hasOwnProperty("snippet")) {
          response.data.items[0].channelInfo = response.data.items[0].snippet;
          delete response.data.items[0].snippet;
        }
    
        return (this.channels = { ...response.data.items[0], ...videos });
      }
    
    
}

export default Youtube;