import React, { memo } from 'react';
import styles from './video_item.module.css';



const VideoItem = memo(({ video, video: { snippet, statistics, channelInfo }, onVideoClick, display }) => {
  const displayType = display === 'list' ? styles.list : styles.grid;
  const count = (count) => {
          
          let num = '';
          if ( count < 1000 ) {
              return count
          } else if (count < 10000) {
              num = count / 1000;
              count = num.toFixed(1)+"천";
              return count
          } else if (count < 100000) {
              num = count / 10000;
              count = num.toFixed(1)+"만";
              return count
          } else if (count < 100000000) {
              count = Math.floor(count / 10000)+"만";
              return count
          } else {
              count = Math.floor(count / 100000000)+"억";
              return count
          }
          
      }
  const date = new Date();
  const uploadDate = new Date(snippet.publishedAt);
  const iso = date.toISOString();
  const currentTime = date.getTime(iso);
  const uploadTime = uploadDate.getTime();
  const timeGap = currentTime - uploadTime;

  const hours = new Date(timeGap);
  const displayTime = hours.getHours();

  const checkTime = () => {
    if (displayTime === 0) {
      return "조금";
    } else if (displayTime > 24) {
      return displayTime / 24 + "일";
    } else {
      return displayTime + "시간";
    }
  };
  return (
  <li className={`${styles.container} ${displayType}`} onClick={()=> onVideoClick(video)}>
      <div className={styles.video}>
          <img className={styles.thumbnail} src={snippet.thumbnails.medium.url} alt="video thumbnail"/>
          <div className={styles.metadata}>
              <p className={styles.title}>{snippet.title}</p> 
              <div className={styles.profile}>
                <img className={styles.channelImg} src={channelInfo.thumbnails.default.url} alt=""/>
                <p className={styles.channel}>{snippet.channelTitle}</p>   
              </div>
              <div className={styles.statistics}>
                  <p className={styles.count}>조회수 {count(statistics.viewCount)}회</p>
                  <p>{checkTime()}전</p>
              </div>
          </div>
      </div>
      
  </li>
  );
})

export default VideoItem;


